export default {
	props: {
		text: String, //显示文本
		rotate: {
			//是否启动加载
			type: [Boolean, String],
			default:false
		},
		bgColor: {
			//按钮背景颜色
			type: String,
			default: 'linear-gradient(to right, rgba(0,0,0,0.7), rgba(0,0,0,0.6))'
		},
		fontColor: {
			//按钮字体颜色
			type: String,
			default: '#FFFFFF'
		}
	},
	data(){
		return{
			visibility:true,
		}
	},
	methods:{
		tap(e){
			this.$emit('buttonTap',e);
		}
	},
	computed: {
		_rotate() {
			//处理值
			return String(this.rotate) !== 'false';
		}
	},
	watch:{
		rotate(){
			setTimeout(()=>this.visibility = !this.rotate,500)
		}
	}
};
